.business__container {
  width: 100%;
  background: #fff;
  box-shadow: 0px 2px 4px grey;
  border-radius: 40px;
  padding: 64px 32px 21px;
  box-sizing: border-box;
}

.business-list__container {
  display: grid;
  grid-template-columns: 20rem 20rem;
  grid-row-gap: 20px;
  grid-column-gap: 20px;
  margin-left: 45rem;
}